body {
  margin: 0;
  padding: 0;
  text-align: center;
  background: -webkit-gradient(linear, left top, left bottom, from(#222), to(#111)) no-repeat center fixed;
  background: -webkit-linear-gradient(#222, #111) no-repeat center fixed;
  background: -o-linear-gradient(#222, #111) no-repeat center fixed;
  background: linear-gradient(#222, #111) no-repeat center fixed;
  color: #fff;
  font-family: 'Permanent Marker', cursive;
}



